@font-face {
  font-family: 'Molde Semi Condensed';
  src: local('Molde Semi Condensed Semibold'),
    local('Molde-Semi-Condensed-Semibold'),
    url('./assets/fonts/Molde-SemiCondensed-Semibold.woff2') format('woff2'),
    url('./assets/fonts/Molde-SemiCondensed-Semibold.woff') format('woff'),
    url('./assets/fonts/Molde-SemiCondensed-Semibold.ttf') format('truetype');
  font-weight: 600;
  font-style: normal;
}

:root {
  --main-font: 'Inter';
  --primary-color: #14096d;
  --secondary-color: #fc7a1e;
  --text-color: #485696;
  /* hamburger menu var  */
  --bar-width: 40px;
  --bar-height: 4px;
  --hamburger-gap: 6px;
  --h-foreground: #14096d;
  --h-background: white;
  --animation-timing: 200ms ease-in-out;
  --hamburger-height: calc(var(--bar-height) * 3 + var(--hamburger-gap) * 2);
  --x-width: calc(var(--hamburger-height) * 1.41421356);
}

/* utility class */

.hidden {
  visibility: hidden;
}

/* css resert  */
/* 
  1. Use a more-intuitive box-sizing model.
*/
*,
*::before,
*::after {
  box-sizing: border-box;
}
/*
  2. Remove default margin
*/
* {
  margin: 0;
}
html {
  scroll-behavior: smooth;
}
/*
  Typographic tweaks!
  3. Add accessible line-height
  4. Improve text rendering
*/
body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;

  min-height: 100vh;
}
/*
  5. Improve media defaults
*/
img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}
/*
  6. Remove built-in form typography styles
*/
input,
button,
textarea,
select {
  font: inherit;
}
/*
  7. Avoid text overflows
*/
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}
/*
  8. Create a root stacking context
*/

