}

/* kurs template */

.frame-kurs {
  width: 1100px;
  height: 96vh;
  background-color: rgb(244, 244, 244);
  color: #485696;
  z-index: 200;
  padding: 30px;
  border-radius: 16px;
}
.frame-kurs .kurs-content {
  display: flex;
  flex-direction: row;
}
.kurs-forma form {
  position: relative;
}

.kurs-info {
  width: 60%;
}

.kurs-info .kurs-data {
  flex-direction: column;
  padding: 0;
  list-style: none;
  font-family: 'Inter';
  font-weight: 600;
}

.kurs-info p {
  text-align: start;
}

.kurs-info .kurs-lekcije li {
  font-family: var(--main-font);
}

.kurs-ostalo {
  text-align: start;
  font-family: var(--main-font);
}

.kurs-forma {
  width: 40%;
  padding-left: 3rem;
  font-family: var(--main-font);
}
.kurs-forma form {
  margin-top: 3rem;
}

.kurs-forma h4 {
  font-size: 1.1rem;
  font-weight: 400;
  text-transform: uppercase;
}

/* blog template */

.blog-template {
  overflow-y: scroll;
  width: 1100px;
  height: 96vh;
  background-color: rgb(244, 244, 244);
  color: #485696;
  z-index: 200;
  padding: 30px;
  border-radius: 16px;
}

